import React, { useState, useEffect } from "react";
import DateRangeIcon from "@mui/icons-material/DateRange";
import { useGlobalState } from "../../hooks/globalState";
import Chip from "../../components/Chip";
import ExploreModal from "../../components/Explore";
import StoreIcon from "@mui/icons-material/Store";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import SearchIcon from "@mui/icons-material/Search";
import FavoriteIcon from "@mui/icons-material/Favorite";
import api from '../../services/api';
import Button from "../../components/Button";
import Product from "../../components/Product";
import FinishOrder from "../../components/FinishOrder";
import "./style.css";

export default function Products() {
  const { setShowExplore, showExplore, cart } = useGlobalState();
  const [ showButton, setShowButton ] = useState(true);
  const [ showFinishModal, setShowFinishModal ] = useState(false);
  const [ products, setProducts ] = useState([]);

  async function getProducts() {
    const { data } = await api.get(`/product/${cart.date}/${cart.hour}`);
    console.log(data)
    setProducts(
      data.sort((a, b) => a.exibitionOrder - b.exibitionOrder)
    );
  }

  useEffect(() => {
    getProducts()
  }, [])

  return (
    <div id="products">
      <div className="header">
        <div className="user">Ol√°, Adoan!</div>
        <div className="explore" onClick={() => setShowExplore(true)}>
          explorar
        </div>
      </div>
      <div className="welcome-msg">
        Vamos montar <br />
        sua primeira caixinha!
      </div>
      <div className="delivery-type">
        <div className="select selected">
          <DateRangeIcon />
          <p>Agende a data e hor√°rio para retirar</p>
        </div>
        <div className="select disabled">
          <div className="more">
            <MoreHorizIcon />
          </div>
          <StoreIcon />
          <p>Retirar em: Americana/SP.</p>
        </div>
      </div>
      <div className="search">
        <input placeholder="O que est√° procurando?" />
        <SearchIcon />
      </div>
      <div className="categories">
        <Chip icon={<FavoriteIcon />} className="category" />
        <Chip label={"Edi√ß√£o limitada"} className="category selected" />
        <Chip label={"Cookies"} className="category" />
        <Chip label={"Bebidas"} className="category" />
        <Chip label={"Sorvetes"} className="category" />
        <Chip label={"Brioches"} className="category" />
      </div>
      <div className="products">
        {products.map(product => <Product product={product} />)}

      </div>
      <div className="btn-space" />
      {showButton && !showExplore && !showFinishModal && (
        <Button
          kind={"primary fixed"}
          label="Fechar minha caixinha"
          onClick={() => setShowFinishModal(true)}
        />
      )}

      <ExploreModal />
      <FinishOrder
        showFinishModal={showFinishModal}
        setShowFinishModal={setShowFinishModal}
      />
    </div>
  );
}
