import React, { useState, useEffect } from "react";

import Brand from "../../components/Brand";
import Button from "../../components/Button";
import Input from '../../components/Input'
import OrderDate from '../../components/OrderDate';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { useGlobalState } from "../../hooks/globalState";
import api from '../../services/api'

import "./style.css";

export default function Order() {
  const { setShowExplore, cart, updateCart, setHours, setAvailableDates } = useGlobalState();
  const [ cities, setCities ] = useState([]);
  const [ showModal, setShowModal ] = useState(false);

  async function getCities() {
    const { data } = await api.get("/city");
    setCities(data);
  }

  async function getCepInfo() {
    const { data } = await api.get(`city/${cart.city}/zip_set`, {
      params: { cep: cart.cep }
    })
    if (data.zip_set) {
      updateCart('zipSet', data.zip_set)
      getAvailableDates()
      getHours()
      setShowModal(true);
    }
    // todo: handle not covered cep
  }

  async function getHours() {
    const { data } = await api.get(`hours`);
    setHours(data)
  }

  async function getAvailableDates() {
    const { data } = await api.get(`city/${cart.city}/checkout`, {
      params: { cep: cart.cep },
    });
    let dates = Object.fromEntries(
      Object.entries(data.data.dates).slice(0, 30)
    )
    setAvailableDates(dates)
    updateCart('date', Object.keys(dates)[ 0 ])
  }

  useEffect(() => {
    getCities()
  }, [])

  useEffect(() => {
    if (cart.cep?.length === 8) getCepInfo();
  }, [ cart.cep ])

  return (
    <div id="order">
      <div className="explore" onClick={() => setShowExplore(true)}>
        explorar
      </div>
      <div className="brand-order common">
        <Brand />
        <div className="content-area">
          <div className="header">
            <h3>Vamos pedir!</h3>
          </div>
          <div className="infos">
            <p>
              Gostaria de receber seu pedido em sua casa ou retirar em loja?
            </p>
          </div>
          <Button kind={cart.delivery === 'delivery' ? "selected" : "primary"} label="Entrega" onClick={() => updateCart('delivery', 'delivery')} />
          <Button kind={cart.delivery === 'withdrawl' ? "selected" : "secondary"} label="Retirada" onClick={() => updateCart('delivery', 'withdrawl')} />

          {cart.delivery === 'delivery' &&
            <select className="custom-select" onChange={(e) => updateCart('city', e.target.value)} defaultValue={0} value={cart.city} >
              <option value={0} disabled>Selecione a cidade</option>
              {cities.map(city => <option value={city.id}>{city.name}</option>)}
            </select>}

          {cart.city &&
            <Input
              icon={<LocationOnIcon className='icon' />}
              placeholder={"Insira o CEP"}
              value={cart.cep}
              onChange={(e) => updateCart('cep', e.target.value)}
              lineColor="black"

            />}

        </div>
      </div>
      <OrderDate showModal={showModal} setShowModal={setShowModal} />
    </div>
  );
}
