import React, { useState } from "react";
import Brand from "../../components/Brand";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import SignIn from "./SignIn";
import SignOn from "./SignOn";
import TextButton from "../TextButton";
import { useGlobalState } from "../../hooks/globalState";
import "./style.css";

export default function ContentLogin() {
  const { setShowExplore } = useGlobalState();

  const [component, setComponent] = useState("signin");
  const signInClass = component === "signin" ? "active" : "disabled";
  const signOnClass = component === "signon" ? "active" : "disabled";
  return (
    <div className="content-login">
      {component === "signin" && (
        <div className="explore" onClick={() => setShowExplore(true)}>
          explorar
        </div>
      )}

      <div
        className="common"
        style={{ marginTop: component === "signin" ? "2vh" : "" }}
      >
        {component === "signin" && <Brand />}
        <div
          className="content-area"
          style={{ marginTop: component === "signin" ? "15vh" : "" }}
        >
          <div className="header-login">
            <ArrowBackIosNewIcon className="arrowBack" />
            <TextButton
              kind={signInClass}
              label={"Fa√ßa login"}
              onClick={() => setComponent("signin")}
            />
            <TextButton
              kind={signOnClass}
              label={"Cadastre-se"}
              onClick={() => setComponent("signon")}
            />
          </div>
          {component === "signin" ? <SignIn /> : <SignOn />}
        </div>
      </div>
    </div>
  );
}
