import React, { useState, useEffect } from "react";

import { useGlobalState } from "../../hooks/globalState";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import FavoriteRoundedIcon from "@mui/icons-material/FavoriteRounded";
import heartUnselected from "../../images/icons/heart-unselected.svg";
import heartSelected from "../../images/icons/heart-selected.svg";
import image from "../../images/cookie.png";

import "./style.css";

export default function Product({ product }) {
  const { setShowExplore } = useGlobalState();
  const [ favorite, setFavorite ] = useState(false);
  return (
    <div className="product">
      <img src={image} alt="" />
      <div
        className="favorite"
        onClick={() => setFavorite((currentFavorite) => !currentFavorite)}
      >
        <img src={favorite ? heartSelected : heartUnselected} alt="" />
      </div>
      <div className="infos">
        <p className="category">{product.category_name}</p>
        <p className="name">{product.name}</p>
        <div>
          <p className="price">{product.price}</p>
          <div className="add">
            <AddCircleIcon />
          </div>
        </div>
      </div>
    </div>
  );
}
