import React, { useState, } from "react";
import FirstWelcome from "../../components/FirstWelcome";
import Post from "../../components/Post";
import Button from "../../components/Button";
import ExploreModal from "../../components/Explore";
import { useGlobalState } from "../../hooks/globalState";
import { useHistory } from 'react-router-dom';

import "./style.css";

export default function Welcome() {
  const history = useHistory();
  const { showExplore } = useGlobalState();
  const posts = [ 1, 2 ];
  const [ hideButton, setHideButton ] = useState(true);
  const [ hideSecondaryButton, setHideSecondaryButton ] = useState(true);

  return (
    <>
      <FirstWelcome />
      {posts.map((v, i) => (
        <Post
          index={i}
          maxIndex={posts.length - 1}
          setHideButton={setHideButton}
          setHideSecondaryButton={setHideSecondaryButton}
          length={posts}
        />
      ))}
      <div
        className={
          hideSecondaryButton || showExplore
            ? "normal-button common fadeout"
            : "normal-button common"
        }
      >
        <Button
          kind="secondary"
          className="common"
          label="Veja as novidades Mille Cuca!"
        />
      </div>

      <div
        className={
          hideButton || showExplore
            ? "sticky-button common fadeout"
            : "sticky-button common"
        }
      >
        <Button kind="primary" label="Pe√ßa j√°!" onClick={() => history.push('/order')} />
      </div>
      {showExplore && <ExploreModal />}
    </>
  );
}
