import React, { useState, useEffect } from "react";
import AddCircleRoundedIcon from "@mui/icons-material/AddCircleRounded";
import RemoveCircleRoundedIcon from "@mui/icons-material/RemoveCircleRounded";
import image from "../../images/banner.jpeg";
import Button from "../../components/Button";

import arrow from "../../images/icons/arrow.svg";
import share from "../../images/icons/share.svg";
import heart from "../../images/icons/heart.svg";

import "./style.css";

export default function Product() {
  async function handleShare() {
    if (navigator.share) {
      try {
        let response = await fetch(
          "https://static.wixstatic.com/media/b1624b_9f77f77150d147d7a250bcdfc3e92811~mv2.jpg/v1/crop/x_0,y_273,w_1480,h_1481/fill/w_396,h_396,al_c,q_80,usm_0.66_1.00_0.01,enc_auto/MilleCuca.jpg"
        );
        let data = await response.blob();
        let metadata = {
          type: "image/png",
        };
        let file = new File([data], "test.jpg", metadata);
        await navigator.share({
          title: "Mille Cuca",
          url: "https://millecuca.com",
          text: "Compra esse cookie pra mim?",
          files: [file],
        });

        /*
          Show a message if the user share something
        */
        alert(`Thanks for Sharing!`);
      } catch (err) {
        /*
          This error will appear if the user cancel the action of sharing.
        */
        alert(`nao quero ${err}`);
      }
    } else {
      alert(`Not supported !!`);
    }
  }
  return (
    <div id="product">
      <img src={image} alt="" />
      <div className="options">
        <div>
          <img src={arrow} alt="" />
        </div>
        <div onClick={handleShare}>
          <img src={share} alt="" />
        </div>
        <div>
          <img src={heart} alt="" />
        </div>
      </div>
      <div className="content-area">
        <div className="infos">
          <h4>Cookie</h4>
          <h1>Mille Pistache</h1>
          <p>
            Cookie tradicional recheado com creme e pasta de pistache 100%
            preparado na nossa cozinha, chocolate branco e pistache granulado.
          </p>
          <h4 className="ingredients">Principais ingredientes:</h4>
          <p>
            Cookie tradicional recheado com creme e pasta de pistache 100%
            preparado na nossa cozinha, chocolate branco e pistache granulado.
          </p>
        </div>
        <div className="infos price">
          <div className="value">
            <p>Pre√ßo:</p>
            <p>12,90</p>
          </div>
          <div className="add-remove">
            <div className="icon remove">
              <RemoveCircleRoundedIcon />
            </div>
            <h3>02</h3>
            <div className="icon add">
              <AddCircleRoundedIcon />
            </div>
          </div>
        </div>
        <Button kind="primary" label={"Adicionar √† caixinha"} />
      </div>
    </div>
  );
}
