import React, { useEffect, useState } from "react";
import EditRoundedIcon from "@mui/icons-material/EditRounded";
import CloseIcon from "@mui/icons-material/Close";

import Button from "../Button";
import { useGlobalState } from "../../hooks/globalState";
import { useHistory } from 'react-router-dom';
import "./style.css";
import { useScrollTrigger } from "@mui/material";
import { Close, DataArraySharp } from "@mui/icons-material";

const weekDays = [ "DOM", "SEG", "TER", "QUA", "QUI", "SEX", "S√ÅB" ];

export default function OrderDate({ showModal, setShowModal }) {
    const history = useHistory();
    const { availableDates, hours, cart, updateCart } = useGlobalState();

    const availableHours = hours.filter(hour => availableDates[ cart.date ].some(date => date.hour === hour.id));

    function getWeekDay(date) {
        const givenDate = new Date(`${date}T12:00:00Z`)
        const today = new Date()

        if (givenDate.toLocaleDateString() === today.toLocaleDateString()) return "Hoje"
        return weekDays[ givenDate.getDay() ]
    }

    function getMonthDay(date) {
        return date.split('-')[ 2 ]
    }

    function getHour(hour) {
        let from = hour.from_time.split(':')
        let to = hour.to_time.split(':')
        return `${from[ 0 ]}h${from[ 1 ]} √†s ${to[ 0 ]}h${to[ 1 ]}`
    }

    return (
        <div id="order-date" hidden={!showModal}>
            <div className="content">
                <div className="close" onClick={() => setShowModal(false)}>
                    <CloseIcon />
                </div>
                <h4>Selecione data e hor√°rio:</h4>
                <div className="days">
                    {Object.keys(availableDates).map(date => <div className={cart.date === date ? 'day selected' : 'day'} onClick={() => {
                        updateCart('date', date);
                        updateCart('hour', null)
                    }}>
                        <p>{getWeekDay(date)}</p>
                        <h5>{getMonthDay(date)}</h5>
                    </div>)}
                </div>
                <div className="hours">
                    {availableHours.map(hour => <div className={cart.hour === hour.id ? 'hour selected' : 'hour'} onClick={() => updateCart('hour', hour.id)}>
                        <p>{getHour(hour)}</p>
                    </div>)}
                </div>
                {/* <h5>Retirar em: Americana, 16 de junho √†s 14h00</h5> */}

                <Button kind={"primary"} label="Continuar" onClick={() => history.push('/products')} />
            </div>
        </div>
    );
}
