import React from "react";
import "antd/dist/antd.css";
import { Input } from "antd";

import "./style.css";

export default function InputComponent({
  label,
  icon,
  type,
  placeholder,
  value,
  onChange,
  lineColor,
}) {
  return (
    <div className="field">
      <label>{label}</label>
      <div className="input">
        {icon}
        <Input
          type={type}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
        />
      </div>
      <div className="line" style={{ backgroundColor: lineColor }} />
    </div>
  );
}
